import axios from "axios";


const URL = "assign/";

export const assignService = {
    create,
    getAllAssigning,
    closeAssign,
    // update,
    // delete: _delete
};

function create(token, assign) {
    return axios.post(process.env.VUE_APP_API_URL + URL + 'add', assign, {
            headers: { 'authorization': token },
        })
        .then(response => {
            return response.data
        })
        .catch(function(error) {
            console.log(error);
            return null;
        })
}

function getAllAssigning(token, currentPage) {
    return axios.get(process.env.VUE_APP_API_URL + URL + 'list', {
            headers: { 'authorization': token },
            params: {
                currentPage: currentPage
            },
        })
        .then(response => {
            return response.data
        })
        .catch(function(error) {
            console.log(error);
            return null;
        })
}

function closeAssign(token, assign) {
    return axios.put(process.env.VUE_APP_API_URL + URL + 'close', assign, {
            headers: { 'authorization': token },
        })
        .then(response => {
            return response.data
        })
        .catch(function(error) {
            console.log(error);
            return null;
        })
}