<style scoped>
.map,
iframe {
  width: 100%;
  height: 300px;
}
tr .text-green {
  color: #43a047;
  position: relative;
}
.text-green:before {
  content: "";
  width: 12px;
  height: 12px;
  background-color: #43a047;
  display: block;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 18px;
  margin-left: -6px;
}
.btn_close {
  position: absolute;
  right: 0;
  top: 0;
}
.item_title {
  width: 100px;
  display: inline-block;
  padding: 12px 16px;
  background-color: #f5f5f5;
  height: 48px;
  line-height: 24px;
  vertical-align: middle;
}
.item_title.date {
  width: 160px;
}
.cross {
  width: 25px;
  display: inline-block;
  padding: 12px 0 12px 16px;
}
.unit {
  width: 44px;
  display: inline-block;
  padding: 12px 0 12px 16px;
}
.item_words {
  width: calc(100% - 100px);
  display: inline-block;
  outline: 1px solid #f5f5f5;
  /* padding: 12px 16px !important; */
}
.item_content {
  width: calc(100% - 100px);
  display: inline-block;
  outline: 1px solid #f5f5f5;
  height: 46px;
  line-height: 24px;
  vertical-align: middle;
  margin-top: 0;
}
.item_words {
  width: calc(100% - 100px);
  display: inline-block;
  outline: 1px solid #f5f5f5;
  /* padding: 8px !important; */
  vertical-align: middle;
  margin-top: 0;
}
.item_content.city,
.item_content.village {
  width: calc((100% - 125px) / 2);
}
.item_content.city {
  margin-right: 25px;
}
.item_content.road {
  width: 100%;
}
.item_content.size {
  width: calc((100% - 257px) / 2);
}
.item_content.date {
  width: calc(100% - 160px);
}
.col {
  padding-top: 0 !important;
}
.theme--light.v-data-table
  > .v-data-table__wrapper
  > table
  > tbody
  > tr:hover:not(.v-data-table__expanded__content):not(.v-data-table__empty-wrapper) {
  background-color: #fff;
}
.status {
  height: 158px;
}
.v-card__actions {
  padding-bottom: 20px;
}
.v-input__slot {
  width: 0 !important;
}
.tableContent table {
  border: 1px solid #efeee9;
}
.tableContent table th {
  background: #efeee9;
  border-right: 1px solid #fff;
  border-bottom: 1px solid #fff !important;
}
.tableContent table th:last-child {
  border-right: 1px solid #efeee9;
}
.tableContent table td {
  border-right: 1px solid #efeee9;
  border-bottom: 1px solid #efeee9;
}
.tableContent table td:first-child {
  border-left: 1px solid #efeee9;
}
</style>

<template>
  <v-row justify="center">
    <v-dialog v-model="isShowAssignInfo" persistent max-width="1200px">
      <v-form ref="form" v-model="valid" lazy-validation>
        <v-card>
          <v-card-title>
            <h3 class="text-center pa-6 mx-auto">派單資料</h3>
          </v-card-title>
          <v-card-text>
            <v-simple-table class="tableContent">
              <template>
                <thead>
                  <tr>
                    <th class="text-center">序號</th>
                    <th class="text-center">捲門狀況</th>
                    <th class="text-center">派單時間</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td class="text-center">{{ selectedAssign.sn }}</td>
                    <!-- <td class="text-center text-red">{{ selectedAssign.doorStatus }}</td> -->
                    <td>
                      <Doorstatus :doorStatus="selectedAssign.doorStatus" />
                    </td>
                    <td class="text-center">
                      {{ formatdDate(selectedAssign.createdAt) }}
                    </td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </v-card-text>

          <v-card class="overflow-hidden" color flat>
            <v-card-text>
              <v-row>
                <v-col cols="4" class="item">
                  <span class="item_title">捲門名稱</span>
                  <v-card-text class="item_content rounded-0">{{
                    selectedAssign.name
                  }}</v-card-text>
                </v-col>
                <v-col cols="4" class="item">
                  <span class="item_title">聯絡人</span>
                  <v-card-text class="item_content rounded-0">{{
                    selectedAssign.contact_person
                  }}</v-card-text>
                </v-col>
                <v-col cols="4">
                  <span class="item_title">手機</span>
                  <v-card-text class="item_content rounded-0">{{
                    selectedAssign.phone
                  }}</v-card-text>
                </v-col>
                <v-col cols="6">
                  <span class="item_title">地址</span>
                  <v-card-text class="item_content rounded-0 city">{{
                    cityStateName.CityName
                  }}</v-card-text>
                  <v-card-text class="item_content rounded-0 village">{{
                    cityStateName.CityStateName
                  }}</v-card-text>
                </v-col>
                <v-col cols="6">
                  <v-card-text class="item_content rounded-0 road">{{
                    selectedAssign.address
                  }}</v-card-text>
                </v-col>
                <v-col cols="6">
                  <span class="item_title">指派車輛</span>
                  <v-card-text class="item_content rounded-0">{{
                    selectedAssign.car_plate
                  }}</v-card-text>
                </v-col>
                <v-col cols="6">
                  <span class="item_title">指派日期</span>
                  <v-card-text class="item_content rounded-0">{{
                    formatAssignedDate(selectedAssign.assigned_date)
                  }}</v-card-text>
                </v-col>
                <v-col cols="12">
                  <span class="item_title status">備註</span>
                  <v-textarea
                    class="item_words status rounded-0"
                    v-if="isEditing"
                    name="input-7-1"
                    outlined
                    dense
                    height="158"
                    hide-details
                    no-resize
                    prop
                  ></v-textarea>
                  <v-card-text class="item_content rounded-0 status" v-else>{{
                    selectedAssign.remark
                  }}</v-card-text>
                </v-col>
              </v-row>
            </v-card-text>

            <v-card-actions align-center class="d-flex justify-center mb-6">
              <v-btn
                v-if="isEditing"
                class="white--text"
                color="#AAA481"
                @click="isEditing = !isEditing"
              >
                編輯完畢
              </v-btn>
              <v-btn
                v-else
                color="blue-grey lighten-4"
                @click="isEditing = !isEditing"
              >
                編輯備註
              </v-btn>

              <v-btn
                v-if="!isEditing"
                class="white--text"
                color="red"
                @click="closeAssign(selectedAssign)"
              >
                <!--                :loading="loading2"
                :disabled="loading2" @click="isEditing = !isEditing" -->
                完成派單
                <template v-slot:loader>
                  <span>刪除中...</span>
                </template>
              </v-btn>
            </v-card-actions>
            <!-- <v-snackbar v-model="hasSaved" :timeout="2000" absolute bottom left
              >Your profile has been updated</v-snackbar
            > -->
          </v-card>
          <v-card-actions class="btn_close">
            <v-icon color="black" text @click="closeDailog(false)"
              >mdi-close-circle</v-icon
            >
          </v-card-actions>
        </v-card>
      </v-form>
    </v-dialog>
  </v-row>
</template>
<script>
import { mapActions, mapState } from "vuex";
import moment from "moment";
import Doorstatus from "./plugins/DoorStatus";

export default {
  status: "AssignInfo",
  // props: ["selectedDoor"],
  components: {
    Doorstatus
  },
  data() {
    return {
      assignList: [
        {
          number: "RTU1233455",
          status: "故障",
          company: "聯上世界",
          car: "ABC-1234",
          time: "2021-12-12  20:30",
          action: "詳細內容",
        },
      ],
      dialog: false,
      isEditing: false,
      valid: false,
    };
  },
  methods: {
    ...mapActions("assignModule", ["showAssignInfoDailog", "closeAssign"]),
    ...mapActions("addressModule", ["getCityAndStateName"]),
    closeDailog() {
      this.showAssignInfoDailog(false);
      this.selected_door = {};
    },
    formatAssignedDate(date) {
      return moment(date).format("YYYY-MM-DD");
    },
    formatdDate(date) {
      return moment(date).format("YYYY-MM-DD HH:mm:ss");
    },
  },
  computed: {
    ...mapState("addressModule", ["cityStateName"]),
    ...mapState("assignModule", ["isShowAssignInfo", "selectedAssign"]),
  },
  watch: {
    selectedAssign() {
      const { city_id, citystate_id } = this.selectedAssign;
      this.getCityAndStateName({ city_id, citystate_id });
    },
    loader() {
      const l = this.loader;
      this[l] = !this[l];

      setTimeout(() => (this[l] = false), 3000);

      this.loader = null;
    },
  },
};
</script>
