  position: absolute;
  right: 0;
  top: 0;
  /* padding-top: 80px; */
}
.v-application--wrap {
  min-height: auto !important;
}
.text-red {
  color: red;
  position: relative;
}
.text-orange {
  color: orange;
  position: relative;
}
.text-yellow {
  color: yellow;
  position: relative;
}
.text-green {
  color: #43a047;
  position: relative;
}
.text-green:before {
  content: "";
  width: 12px;
  height: 12px;
  background-color: #43a047;
  display: block;
  border-radius: 10px;
  position: absolute;
  left: 50%;
