  margin-left: -30px;
}
.text-red:before {
  content: "";
  width: 12px;
  height: 12px;
  background-color: red;
  display: block;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 18px;
  margin-left: -30px;
}
.pageinfo {
  width: 80%;
  max-width: 900px;
  position: fixed;
  bottom: 0;
  left: 50%;
  margin-left: -320px;
}
.theme--light.v-btn.btn_add {
  color: #fff;
}
