<template>
  <v-container>
    <v-row class="mt-4">
      <v-col 
      cols="4"
      >
        <v-text-field
          rounded
          label=""
          placeholder="時間搜尋 / 捲門名稱"
          append-icon="mdi-magnify"
          xsmall
          filled
          background-color="#F3F3F3"
          color="#333333"
          dense
          height="42"
        ></v-text-field>
        
      </v-col>
      <v-col 
      cols="4"
      offset="4"
      >
        <div class="text-right">
          <v-btn
            rounded
            color="#B4B1A1"
            class="btn_add"
            @click="openCreateDailog"
          >
            <v-icon left>
              mdi-plus-circle
            </v-icon>
            新增捲門
          </v-btn>
        </div>
      </v-col>
      <v-col cols="12" class="text-center">
        <v-app id="inspire">
          <v-simple-table>
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-center" width="100">狀態</th>
                  <th class="text-center">狀態更新時間</th>
                  <th class="text-center">捲門名稱</th>
                  <th class="text-center">地址</th>
                  <th class="text-center">Actions</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="item in doors" :key="item.sn">
                  <!-- <td :class="getStatusColor(item.status)">
                    {{ getStatusTitle(item.status) }}
                  </td> -->
                  <td>
                    <Doorstatus :doorStatus="item.status" />
                  </td>
                  <td>{{ item.updatedAt }}</td>
                  <td>{{ item.name }}</td>
                  <td>{{ item.address }}</td>
                  <td>
                    <v-btn text @click="goToInfo(item)">詳細內容</v-btn>
                  </td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </v-app>
      </v-col>
      <div class="pageinfo">
        <v-container class="max-width">
          <v-pagination
            v-model="currentPage"
            class="my-4"
            :length="pagination.totalPage"
            @input="handlePageChange"
          ></v-pagination>
        </v-container>
      </div>
    </v-row>
    <DoorCreate />
    <DoorInfo />
    <!-- <DoorInfo :selected-door="selectedDoor" /> -->
    <!-- <component :is="dailogComponent" ></component> -->
  </v-container>
</template>

<script>
import { mapActions, mapState } from "vuex";
import DoorInfo from "./DoorInfo";
import DoorCreate from "./DoorCreate";
import Doorstatus from "./plugins/DoorStatus";

export default {
  status: "DoorList",
  created() {
    const { currentPage } = this;
    this.getAll({ currentPage });
  },
  components: {
    DoorInfo,
    DoorCreate,
    Doorstatus
  },
  data() {
    return {
      doorlists: [
        {
          sn: "1121321321231231",
          name: "測試111111",
          tax: "1235729",
          fax: "1235729",
          phone: "1235729",
          city_id: "1235729",
          area_id: "1235729",
          address: "1235729",
          status: "1",
          effective_date: "1235729",
          size: "100x2000",
          material: "1",
          location: "2",
          updatedAt: "2021-04-12T09:08:03.000Z"
        }
      ],
      currentPage: 1,
      dialogTag: ""
      // selectedDoor: {},
    };
  },
  methods: {
    ...mapActions("doorsModule", [
      "getAll",
      "chooseDoor",
      "showInfoDailog",
      "showCreateDoorDailog"
    ]),
    goToInfo(door) {
      this.chooseDoor(door);
      this.showInfoDailog(true);
    },
    handlePageChange(currentPage) {
      this.getAll({ currentPage });
    },
    openCreateDailog() {
      this.showCreateDoorDailog(true);
    }
  },
  computed: {
    ...mapState("doorsModule", ["doors", "pagination"]),
    dailogComponent() {
      return this.dialogTag;
    }
  }
};
</script>
