<style scope>
svg {
  width: 100%;
  height: 100vh;
}

.map {
  margin-right: auto;
}

g path {
  transition: 0.5s;
  fill: #e2dfcf;
  stroke: #fff;
}
g path:hover, g path.active {
  fill: #b4b1a1;
  cursor: pointer;
}
g > g{
  position: relative;
}
g > g text{
   dominant-baseline:middle;
   text-anchor:middle;
     cursor: default;
     pointer-events: none;
     display: inline-block;
  
}
g > g path{
  fill: red;
}
g > g path, g > g path text tspan {
  cursor: pointer;
  display: block;
}
.title_area {
  margin: auto;
  color: white;
  font-size: 30px;
  transition: 0.5s;
}
text {
  position: relative;
}
</style>
