<style scoped>
.map,
iframe {
  width: 100%;
  height: 300px;
}
tr .text-green {
  color: #43a047;
  position: relative;
}
.text-green:before {
  content: "";
  width: 12px;
  height: 12px;
  background-color: #43a047;
  display: block;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 18px;
  margin-left: -6px;
}
.btn_close {
  position: absolute;
  right: 0;
  top: 0;
}
.item_title {
  width: 100px;
  display: inline-block;
  padding: 12px 16px;
  background-color: #f5f5f5;
  height: 48px;
  line-height: 24px;
  vertical-align: middle;
}
.item_title.date {
  width: 160px;
}
.cross {
  width: 25px;
  display: inline-block;
  padding: 12px 0 12px 16px;
}
.unit {
  width: 44px;
  display: inline-block;
  padding: 12px 0 12px 16px;
}
.item_words {
  width: calc(100% - 100px);
  display: inline-block;
  outline: 1px solid #f5f5f5;
  /* padding: 12px 16px !important; */
}
.item_content {
  width: calc(100% - 100px);
  display: inline-block;
  outline: 1px solid #f5f5f5;
  height: 46px;
  line-height: 24px;
  vertical-align: middle;
  margin-top: 0;
}
.item_words {
  width: calc(100% - 100px);
  display: inline-block;
  outline: 1px solid #f5f5f5;
  /* padding: 8px !important; */
  vertical-align: middle;
  margin-top: 0;
}
.item_content.city,
.item_content.village {
  width: calc((100% - 125px) / 2);
}
.item_content.city {
  margin-right: 25px;
}
.item_content.road {
  width: 100%;
}
.item_content.size {
  width: calc((100% - 257px) / 2);
}
.item_content.date {
  width: calc(100% - 160px);
}
.col {
  padding-top: 0 !important;
}
.theme--light.v-data-table
  > .v-data-table__wrapper
  > table
  > tbody
  > tr:hover:not(.v-data-table__expanded__content):not(.v-data-table__empty-wrapper) {
  background-color: #fff;
}
.status {
  height: 158px;
}
.v-card__actions {
  padding-bottom: 20px;
}
.v-card__actions > .v-btn.v-btn {
  margin: 0 auto;
}
.v-input__slot {
  width: 0 !important;
}
.tableContent table {
  border: 1px solid #efeee9;
}
.tableContent table th {
  background: #efeee9;
  border-right: 1px solid #fff;
  border-bottom: 1px solid #fff !important;
}
.tableContent table th:last-child {
  border-right: 1px solid #efeee9;
}
.tableContent table td {
  border-right: 1px solid #efeee9;
  border-bottom: 1px solid #efeee9;
}
.tableContent table td:first-child {
  border-left: 1px solid #efeee9;
}
</style>

<template>
  <v-row justify="center">
    <v-dialog v-model="isShowInfo" persistent max-width="1200px">
      <v-form ref="form" v-model="valid" lazy-validation>
        <v-card>
          <v-card-title>
            <h3 class="text-center pa-6 mx-auto">捲門資料</h3>
            <span class="map">
              <iframe
                src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d14462.28307777603!2d121.19664106977541!3d25.014697099999992!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x346821677632f263%3A0xf5660d8237d5a781!2z6Iqx6Iie6LGs77yI6Z-T5byP54Ok6IKJ5ZCD5Yiw6aO977yJ!5e0!3m2!1szh-TW!2stw!4v1618325625599!5m2!1szh-TW!2stw"
                style="border: 0"
                allowfullscreen
                loading="lazy"
              ></iframe>
            </span>
          </v-card-title>

          <v-card-text>
            <v-simple-table class="tableContent">
              <template>
                <thead>
                  <tr>
                    <th class="text-center">序號</th>
                    <th class="text-center">狀態</th>
                    <th class="text-center">狀態更新時間</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td class="text-center">{{ selectedDoor.sn }}</td>
                    <td class="text-center">
                      <Doorstatus :doorStatus="selectedDoor.status"/>
                    </td>
                    <td class="text-center">{{setTime(selectedDoor.updatedAt)}}</td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </v-card-text>

          <v-card class="overflow-hidden" color flat>
            <v-card-text>
              <v-row>
                <v-col cols="4" class="item">
                  <span class="item_title">名稱</span>
                  <v-text-field
                    class="item_content rounded-0"
                    v-if="isEditing"
                    v-model="editDoor.name"
                    :rules="formRules.DoorNameRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                    required
                  ></v-text-field>
                  <v-card-text class="item_content rounded-0" v-else>{{
                    selectedDoor.name
                  }}</v-card-text>
                </v-col>
                <v-col cols="4" class="item">
                  <span class="item_title">客戶名稱</span>
                  <v-text-field
                    class="item_content rounded-0"
                    v-if="isEditing"
                    v-model="editDoor.client_name"
                    :rules="formRules.ClientNameRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                  ></v-text-field>
                  <v-card-text class="item_content rounded-0" v-else>{{
                    selectedDoor.client_name
                  }}</v-card-text>
                </v-col>
                <v-col cols="4" class="item">
                  <span class="item_title">聯絡人</span>
                  <v-text-field
                    class="item_content rounded-0"
                    v-if="isEditing"
                    v-model="editDoor.contact_person"
                    :rules="formRules.ContactPersonRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                  ></v-text-field>
                  <v-card-text class="item_content rounded-0" v-else>{{
                    selectedDoor.contact_person
                  }}</v-card-text>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="4">
                  <span class="item_title">市話</span>
                  <v-text-field
                    class="item_content rounded-0"
                    v-if="isEditing"
                    v-model="editDoor.tax"
                    :rules="formRules.taxRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                  ></v-text-field>
                  <v-card-text class="item_content rounded-0" v-else>{{
                    selectedDoor.tax
                  }}</v-card-text>
                </v-col>
                <v-col cols="4">
                  <span class="item_title">手機</span>
                  <v-text-field
                    class="item_content rounded-0"
                    v-if="isEditing"
                    v-model="editDoor.phone"
                    :rules="formRules.taxRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                  ></v-text-field>
                  <v-card-text class="item_content rounded-0" v-else>{{
                    selectedDoor.phone
                  }}</v-card-text>
                </v-col>
                <v-col cols="4">
                  <span class="item_title">傳真</span>
                  <v-text-field
                    class="item_content rounded-0"
                    v-if="isEditing"
                    v-model="editDoor.fax"
                    :rules="formRules.taxRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                  ></v-text-field>
                  <v-card-text class="item_content rounded-0" v-else>{{
                    selectedDoor.fax
                  }}</v-card-text>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="6">
                  <span class="item_title">地址</span>

                  <v-select
                    class="item_content rounded-0 city"
                    v-if="isEditing"
                    :items="citys"
                    item-text="name"
                    item-value="city_id"
                    v-model="editDoor.city_id"
                    :rules="formRules.selectRules"
                    @change="selectCity"
                    outlined
                    dense
                    height="47"
                    hide-details
                  ></v-select>
                  <v-card-text class="item_content rounded-0 city" v-else>{{
                    selectedDoor.cityName.name
                  }}</v-card-text>

                  <v-select
                    class="item_content rounded-0 village"
                    v-if="isEditing"
                    :items="cityStates"
                    item-text="name"
                    item-value="id"
                    v-model="editDoor.citystate_id"
                    :rules="formRules.selectRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                  ></v-select>
                  <v-card-text class="item_content rounded-0 village" v-else>{{
                    selectedDoor.citystateName.name
                  }}</v-card-text>
                </v-col>
                <v-col cols="6">
                  <v-text-field
                    class="item_content rounded-0 road"
                    v-if="isEditing"
                    v-model="editDoor.address"
                    :rules="formRules.addressRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                  ></v-text-field>
                  <v-card-text class="item_content rounded-0 road" v-else>{{
                    selectedDoor.address
                  }}</v-card-text>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="6">
                  <span class="item_title">簽約方案</span>
                  <v-select
                    class="item_content rounded-0"
                    v-if="isEditing"
                    :items="contract"
                    item-text="name"
                    item-value="id"
                    v-model="editDoor.contract"
                    :rules="formRules.selectRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                  ></v-select>
                  <v-card-text class="item_content rounded-0" v-else>{{
                    setContractName(selectedDoor.contract)
                  }}</v-card-text>
                </v-col>
                <v-col cols="6">
                  <span class="item_title date">合約起始有效期限</span>
                  <v-menu
                    ref="menu"
                    v-model="datePickerMenu"
                    :close-on-content-click="false"
                    :return-value.sync="selectedDoor.effective_date"
                    transition="scale-transition"
                    offset-y
                    max-width="290"
                    min-width="auto"
                    v-if="isEditing"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="selectedDoor.effective_date"
                        label=""
                        prepend-inner-icon="mdi-calendar"
                        readonly
                        v-bind="attrs"
                        v-on="on"
                        class="item_content rounded-0 date rounded-0"
                        outlined="white"
                        dense
                        height="47"
                        hide-details
                      ></v-text-field>
                    </template>
                    <v-date-picker
                      v-model="selectedDoor.effective_date"
                      no-title
                      scrollable
                    >
                      <v-spacer></v-spacer>
                      <v-btn
                        text
                        color="primary"
                        @click="datePickerMenu = false"
                      >
                        Cancel
                      </v-btn>
                      <v-btn
                        text
                        color="primary"
                        @click="$refs.menu.save(selectedDoor.effective_date)"
                      >
                        OK
                      </v-btn>
                    </v-date-picker>
                  </v-menu>
                  <v-card-text class="item_content date" v-else>{{
                    selectedDoor.effective_date
                  }}</v-card-text>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="3">
                  <span class="item_title">材質</span>
                  <v-select
                    class="item_content rounded-0"
                    v-if="isEditing"
                    :items="door_texture"
                    item-text="name"
                    item-value="id"
                    v-model="editDoor.door_material"
                    :rules="formRules.selectRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                  ></v-select>
                  <v-card-text class="item_content rounded-0" v-else>{{
                    setDoorTextureName(selectedDoor.door_material)
                  }}</v-card-text>
                </v-col>
                <v-col cols="3">
                  <span class="item_title">門型</span>
                  <v-select
                    class="item_content rounded-0"
                    v-if="isEditing"
                    :items="door_style"
                    item-text="name"
                    item-value="id"
                    v-model="editDoor.door_type"
                    :rules="formRules.selectRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                  ></v-select>
                  <v-card-text class="item_content rounded-0" v-else>{{
                    setDoorTypeName(selectedDoor.door_type)
                  }}</v-card-text>
                </v-col>
                <v-col cols="6">
                  <span class="item_title">尺寸</span>
                  <span class="unit">寬</span>
                  <v-text-field
                    class="item_content rounded-0 size"
                    v-if="isEditing"
                    v-model="editDoor.door_width"
                    outlined
                    dense
                    height="47"
                    hide-details
                  ></v-text-field>
                  <v-card-text class="item_content rounded-0 size" v-else>{{
                    selectedDoor.door_width
                  }}</v-card-text>
                  <span class="cross">X</span>
                  <span class="unit">高</span>
                  <v-text-field
                    class="item_content rounded-0 size"
                    v-if="isEditing"
                    v-model="editDoor.door_height"
                    outlined
                    dense
                    height="47"
                    hide-details
                  ></v-text-field>
                  <v-card-text class="item_content rounded-0 size" v-else>{{
                    selectedDoor.door_height
                  }}</v-card-text>
                  <span class="unit">cm</span>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="12">
                  <span class="item_title status">備註</span>
                  <v-textarea
                    class="item_words status rounded-0"
                    v-if="isEditing"
                    name="input-7-1"
                    v-model="editDoor.remarks"
                    outlined
                    dense
                    height="158"
                    hide-details
                    no-resize
                    prop
                  ></v-textarea>
                  <v-card-text class="item_content rounded-0 status" v-else>{{
                    selectedDoor.remarks
                  }}</v-card-text>
                </v-col>
              </v-row>
            </v-card-text>
            <v-card-actions align-center>
              <v-btn v-if="isEditing" @click="confirmEdit()" color="success">
                確認編輯
              </v-btn>
              <v-btn v-else @click="enterEdit" color="blue-grey lighten-4">
                編輯資料
              </v-btn>
            </v-card-actions>
            <v-snackbar v-model="hasSaved" :timeout="2000" absolute bottom left
              >Your profile has been updated</v-snackbar
            >
          </v-card>
          <v-card-actions class="btn_close">
            <v-icon color="black" text @click="closeInfoPage"
              >mdi-close-circle</v-icon
            >
          </v-card-actions>
        </v-card>
      </v-form>
    </v-dialog>
  </v-row>
</template>
<script>
import { mapActions, mapState } from "vuex";
import { formRules } from "../plugins/formRules";
import { selectsItems } from "../plugins/SelectsItems";
import Doorstatus from "./plugins/DoorStatus";
import moment from "moment";

export default {
  status: "DoorInfo",
  // props: ["selectedDoor"],
  components:{
    Doorstatus
  },
  data() {
    return {
      contract: selectsItems.contractSelectsItems,
      door_texture: selectsItems.materialSelectsItems,
      door_style: selectsItems.typeSelectsItems,
      hasSaved: false,
      isEditing: false,
      cityName: "",
      cityStateName: "",
      contractName: "",
      doorTypeName: "",
      doorTextureName: "",
      editDoor: {},
      valid: false,
      formRules: formRules,
      datePickerMenu: false,
    };
  },
  created() {},
  methods: {
    ...mapActions("doorsModule", ["showInfoDailog", "setDoorInfo"]),
    ...mapActions("addressModule", [
      "getCityList",
      "getCityStatesList",
      "getCityName",
    ]),
    goToInfo(door) {
      this.chooseDoor(door);
      this.showInfoDailog(true);
    },
    handlePageChange(currentPage) {
      this.getAll({ currentPage });
    },
    openCreateDailog() {
      this.showCreateDoorDailog(true);
    },
    enterEdit() {
      this.isEditing = !this.isEditing;
      this.editDoor = { ...this.selectedDoor };
    },
    confirmEdit() {
      let validate = this.$refs.form.validate();
      if (validate) {
        this.isEditing = !this.isEditing;
        console.log("confirm:", this.editDoor);
        this.setDoorInfo(this.editDoor);
      }
    },
    selectCity(city_id) {
      this.editDoor.city_id = city_id;
      this.editDoor.citystate_id = null;
      this.getCityStatesList(city_id);
    },
    setContractName(contract_id) {
      if (contract_id == null) return "";
      return (this.contractName = this.contract.find(
        (contract) => contract.id == contract_id
      ).name);
    },
    setDoorTypeName(type_id) {
      if (type_id == null) return "";
      return (this.contractName = this.door_style.find(
        (style) => style.id == type_id
      ).name);
    },
    setDoorTextureName(texture_id) {
      if (texture_id == null) return "";
      return (this.doorTextureName = this.door_texture.find(
        (texture) => texture.id == texture_id
      ).name);
    },
    closeInfoPage() {
      this.isEditing = false;
      this.showInfoDailog(false);
    },
    setTime(time_str){
      return moment(time_str).format("YYYY-MM-DD HH:mm:ss")
    }
  },
  computed: {
    ...mapState("addressModule", ["citys", "cityStates"]),
    ...mapState("doorsModule", ["isShowInfo", "selectedDoor"]),
  },
  watch: {
    selectedDoor() {
      this.getCityList();
      this.getCityStatesList(this.selectedDoor.city_id);
    },
    // citys() {
    //   if (!this.isEditing)
    //     this.setCityName(this.selectedDoor.city_id);
    // },
    // cityStates() {
    //   if (!this.isEditing)
    //     this.setCityStateName(this.selectedDoor.citystate_id);
    // },
  },
};
</script>
