import Vue from "vue";
import VueRouter from "vue-router";
// import Home from "../views/Home.vue";
// import DoorList from "../components/DoorList.vue";

Vue.use(VueRouter);

export const routes = [{
        path: "/",
        redirect: '/mapUrgent',
    },
    {
        path: "/about",
        name: "About",
        component: () =>
            import ( /* webpackChunkName: "about" */ "../views/About.vue")
    },
    {
        path: "/mapUrgent",
        name: "mapUrgent",
        component: () =>
            import ( /* webpackChunkName: "about" */ "../components/mapUrgent.vue")
    },
    {
        path: "/mapArea",
        name: "mapArea",
        component: () =>
            import ( /* webpackChunkName: "about" */ "../components/mapArea.vue")
    },
    {
        path: "/doorList",
        name: "DoorList",
        component: () =>
            import ( /* webpackChunkName: "about" */ "../components/DoorList.vue")
    },
    {
        path: "/carList",
        name: "CarList",
        component: () =>
            import ( /* webpackChunkName: "about" */ "../components/CarList.vue")
    },
    {
        path: "/assignList",
        name: "AssignList",
        component: () =>
            import ( /* webpackChunkName: "about" */ "../components/AssignList.vue")
    },
    {
        path: "/assignRecord",
        name: "AssignRecord",
        component: () =>
            import ( /* webpackChunkName: "about" */ "../components/AssignRecord.vue")
    },
    {
        path: "/logList",
        name: "LogList",
        component: () =>
            import ( /* webpackChunkName: "about" */ "../components/LogList.vue")
    },
    {
        path: "/logDoorList",
        name: "LogDoorList",
        component: () =>
            import ( /* webpackChunkName: "about" */ "../components/LogDoorList.vue")
    },
];

const router = new VueRouter({
    mode: "history",
    base: process.env.BASE_URL,
    routes
});

export default router;