export const statusConvert = {
    getStatusColor: function(status) {
        switch (status) {
            case 0:
                return "red";
            case 1:
                return "orange";
            case 2:
                return "yellow";
            case 3:
                return "green";
            case 4:
                return "blue";
            case 5:
                return "purple";
        }
    },
    getStatusTitle: function(status) {
        switch (status) {
            case 0:
                return "門體故障";
            case 1:
                return "門體異常";
            case 2:
                return "外接異常";
            case 3:
                return "正常";
            case 4:
                return "定期保養";
            case 5:
                return "客戶呼叫";
        }
    },
    getStatusTextColor: function(status) {
        switch (status) {
            case 0:
                return "white";
            case 1:
                return "white";
            case 2:
                return "black";
            case 3:
                return "white";
            case 4:
                return "white";
            case 5:
                return "white";
            default:
                return "white";
        }
    },
};