import { doorService } from '../service/door.service.js';
import config from '../config'

const TAG = "DoorsModule"
const state = {
    door: {},
    doors: [],
    selectedDoor: { cityName: {}, citystateName: {} },
    pagination: {
        currentPage: 1,
        totalItem: 0,
        totalPage: 0
    },
    isShowInfo: false,
    isShowCreateDoor: false,
}

const actions = {
    createNewDoor({ commit, rootState, state, dispatch }, newdoor) {
        let token = rootState.userModule.user.token

        console.log(TAG, "createNewDoor:", newdoor)
        doorService.createNewDoor(token, newdoor)
            .then(
                data => {
                    console.log(TAG, "Create Door:", data)
                        // if (data.code == config.RES_CODE.SUCCESS) {
                        //     let payload = data.data
                        // }
                    commit("showCreateDoorDailog", false)

                    console.log("getCurrentPage:", state.currentPage);
                    let currentPage = state.pagination.currentPage
                    dispatch("getAll", { currentPage })
                },
                error => {
                    console.log(TAG, "Create door:", error)
                }
            )
    },
    getAll({ commit, rootState }, { currentPage }) {
        let token = rootState.userModule.user.token
        doorService.getAll(token, currentPage)
            .then(
                data => {
                    console.log(TAG, "Get Door:", data)
                    if (data.code == config.RES_CODE.SUCCESS) {

                        let payload = data.data
                        commit("getAllDoors", payload.data)
                            // console.log(TAG, "totalPage:", Math.ceil(payload.totalCount / 10))
                        commit("setPagination", {
                            currentPage: currentPage,
                            totalItem: payload.totalCount,
                            totalPage: Math.ceil(payload.totalCount / 10)
                        })
                    }
                },
                error => {
                    console.log(TAG, "vuex get all door:", error)
                }
            )
    },
    getAllDoorByStatus({ commit, rootState }, statusList) {
        let token = rootState.userModule.user.token
        doorService.getAllByStatus(token, statusList)
            .then(
                data => {
                    console.log(TAG, "Get Door By Status:", data)
                    if (data.code == config.RES_CODE.SUCCESS) {
                        let payload = data.data
                        commit("getAllDoors", payload.data)
                        commit("setPagination", {
                            totalItem: payload.totalCount,
                            totalPage: Math.ceil(payload.totalCount / 10)
                        })
                    }
                },
                error => {
                    console.log(TAG, "vuex get all door:", error)
                }
            )
    },
    getDoorsByCityAndStatus({ commit, rootState }, { city_id, status, currentPage }) {
        console.log(TAG, "Get getDoorsByCityAndStatus:", city_id, status)
        let token = rootState.userModule.user.token
        doorService.getAllbyCity(token, status, city_id, currentPage)
            .then(
                data => {
                    console.log(TAG, "Get Door By City:", data)
                    if (data.code == config.RES_CODE.SUCCESS) {
                        let payload = data.data
                        commit("getAllDoors", payload.data)
                        commit("setPagination", {
                            totalItem: payload.totalCount,
                            totalPage: Math.ceil(payload.totalCount / 10)
                        })
                    }
                },
                error => {
                    console.log(TAG, "vuex get all door:", error)
                }
            )
    },
    getDoorInfo({ commit, rootState }, door_id) {
        let token = rootState.userModule.user.token
        doorService.getDoorById(token, door_id)
            .then(
                data => {
                    console.log(TAG, "get Door:", data)
                    if (data.code == config.RES_CODE.SUCCESS) {
                        let payload = data.data
                        commit("setDoor", payload)
                    }
                },
                error => {
                    console.log(TAG, "vuex get all door:", error)
                }
            )
    },
    setDoorInfo({ commit, rootState }, door) {
        let token = rootState.userModule.user.token
        doorService.callEditDoorApi(token, door)
            .then(
                data => {
                    console.log(TAG, "Set Door:", data)
                    if (data.code == config.RES_CODE.SUCCESS) {
                        let payload = data.data
                        console.log(TAG, "set sucess:", payload)
                        commit("upDoorToDoorList", payload)
                    }
                },
                error => {
                    console.log(TAG, "vuex get all door:", error)
                }
            )
    },
    showInfoDailog({ commit }, isShow) {
        commit("showInfoDailog", isShow)
    },
    chooseDoor({ commit }, door) {
        commit("setSelectedDoor", door)
    },
    showCreateDoorDailog({ commit }, isShow) {
        commit("showCreateDoorDailog", isShow)
    },
    closeDoorInfo({ commit }) {
        commit("setDoor", {})
    },
};

const mutations = {
    setDoor(state, door) {
        state.door = door;
    },
    getAllDoors(state, doors) {
        state.doors = doors;
    },
    setPagination(state, payload) {
        state.pagination = payload;
    },
    upDoorToDoorList(state, newdoor) {
        const item = state.doors.find(item => item.sn === newdoor.sn);
        Object.assign(item, newdoor);
    },
    showInfoDailog(state, isShow) {
        state.isShowInfo = isShow;
    },
    setSelectedDoor(state, door) {
        state.selectedDoor = door;
    },
    showCreateDoorDailog(state, show) {
        state.isShowCreateDoor = show;
    }
};

export const doorsModule = {
    namespaced: true,
    state,
    actions,
    mutations
};