<style scoped>
.map,
iframe {
  width: 100%;
  height: 300px;
}
tr .text-green {
  color: #43a047;
  position: relative;
}
.text-green:before {
  content: "";
  width: 12px;
  height: 12px;
  background-color: #43a047;
  display: block;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 18px;
  margin-left: -30px;
}
.btn_close {
  position: absolute;
  right: 0;
  top: 0;
}
.item_title {
  width: 100px;
  display: inline-block;
  padding: 12px 16px;
  background-color: #f5f5f5;
  height: 48px;
  line-height: 24px;
  vertical-align: middle;
}
.item_title.date {
  width: 160px;
}
.cross {
  width: 25px;
  display: inline-block;
  padding: 12px 0 12px 16px;
}
.unit {
  width: 44px;
  display: inline-block;
  padding: 12px 0 12px 16px;
}
.item_content,
.item_words {
  width: calc(100% - 100px);
  display: inline-block;
  outline: 1px solid #f5f5f5;
  /* padding: 12px 16px !important; */
}
.item_content {
  height: 46px;
  line-height: 24px;
  vertical-align: middle;
  margin-top: 0;
}
.item_words {
  width: calc(100% - 100px);
  display: inline-block;
  outline: 1px solid #f5f5f5;
  /* padding: 8px !important; */
  vertical-align: middle;
  margin-top: 0;
}
.item_content.city,
.item_content.village {
  width: calc((100% - 125px) / 2);
}
.item_content.city {
  margin-right: 25px;
}
.item_content.road {
  width: 100%;
}
.item_content.size {
  width: calc((100% - 257px) / 2);
}
.item_content.date {
  width: calc(100% - 160px);
}
.col {
  padding-top: 0 !important;
}
.theme--light.v-data-table
  > .v-data-table__wrapper
  > table
  > tbody
  > tr:hover:not(.v-data-table__expanded__content):not(.v-data-table__empty-wrapper) {
  background-color: #fff;
}
.status {
  height: 158px;
}
.v-card__actions {
  padding-bottom: 20px;
}
.v-card__actions > .v-btn.v-btn {
  margin: 0 auto;
}
</style>

<template>
  <v-row justify="center">
    <v-dialog v-model="isShowCreateDoor" persistent max-width="1200px">
      <v-form ref="form" v-model="valid" lazy-validation>
        <v-card>
          <v-card class="overflow-hidden" color flat>
          <v-card-title>
            <h3 class="text-center pa-6 mx-auto">新增捲門</h3>
          </v-card-title>
            <v-card-text>
              <v-row>
                <v-col cols="6" class="item">
                  <span class="item_title">序號</span>
                  <v-text-field
                    class="item_content rounded-0"
                    label=""
                    v-model="newDoor.sn"
                    :rules="formRules.DoorSNRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="6" class="item">
                  <span class="item_title">捲門名稱</span>
                  <v-text-field
                    class="item_content rounded-0"
                    label=""
                    v-model="newDoor.name"
                    :rules="formRules.DoorNameRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="6" class="item">
                  <span class="item_title">客戶名稱</span>
                  <v-text-field
                    class="item_content rounded-0"
                    label=""
                    v-model="newDoor.client_name"
                    :rules="formRules.ClientNameRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="6" class="item">
                  <span class="item_title">聯絡人</span>
                  <v-text-field
                    class="item_content rounded-0"
                    label=""
                    v-model="newDoor.contact_person"
                    :rules="formRules.ContactPersonRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                    required
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="4">
                  <span class="item_title">市話</span>
                  <v-text-field
                    class="item_content rounded-0"
                    label=""
                    v-model="newDoor.tax"
                    :rules="formRules.taxRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="4">
                  <span class="item_title">手機</span>
                  <v-text-field
                    class="item_content rounded-0"
                    label=""
                    v-model="newDoor.phone"
                    :rules="formRules.taxRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="4">
                  <span class="item_title">傳真</span>
                  <v-text-field
                    class="item_content rounded-0"
                    label=""
                    v-model="newDoor.fax"
                    :rules="formRules.taxRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                    required
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="6">
                  <span class="item_title">地址</span>
                  <v-select
                    class="item_content rounded-0 city"
                    :items="citys"
                    item-text="name"
                    item-value="city_id"
                    v-model="newDoor.city_id"
                    :rules="formRules.selectRules"
                    label=""
                    @change="selectCity"
                    outlined
                    dense
                    height="47"
                    hide-details
                    required
                  ></v-select>

                  <v-select
                    class="item_content rounded-0 village"
                    :items="cityStates"
                    item-text="name"
                    item-value="id"
                    v-model="newDoor.citystate_id"
                    :rules="formRules.selectRules"
                    label=""
                    outlined
                    dense
                    height="47"
                    hide-details
                    required
                  ></v-select>
                </v-col>
                <v-col cols="6">
                  <v-text-field
                    class="item_content rounded-0 road"
                    label=""
                    v-model="newDoor.address"
                    :rules="formRules.selectRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                    required
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="6">
                  <span class="item_title">簽約方案</span>
                  <v-select
                    ref="door_contract"
                    class="item_content rounded-0"
                    :items="door_contract"
                    item-text="name"
                    item-value="id"
                    color=""
                    v-model="newDoor.contract"
                    :rules="formRules.selectRules"
                    label=""
                    outlined
                    dense
                    height="47"
                    hide-details
                    required
                  ></v-select>
                </v-col>
                <v-col cols="6">
                  <span class="item_title date">合約起始有效期限</span>
                  <v-text-field
                    class="item_content rounded-0 date"
                    label=""
                    v-model="newDoor.effective_date"
                    outlined
                    dense
                    height="47"
                    hide-details
                    required
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="3">
                  <span class="item_title">材質</span>
                  <v-select
                    class="item_content rounded-0"
                    :items="door_material"
                    item-text="name"
                    item-value="id"
                    label=""
                    v-model="newDoor.door_material"
                    :rules="formRules.selectRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                    required
                  ></v-select>
                </v-col>
                <v-col cols="3">
                  <span class="item_title">門型</span>
                  <v-select
                    class="item_content rounded-0"
                    :items="door_style"
                    item-text="name"
                    item-value="id"
                    label=""
                    v-model="newDoor.door_type"
                    :rules="formRules.selectRules"
                    outlined
                    dense
                    height="47"
                    hide-details
                    required
                  ></v-select>
                </v-col>
                <v-col cols="6">
                  <span class="item_title">尺寸</span>
                  <span class="unit">寬</span>
                  <v-text-field
                    class="item_content rounded-0 size"
                    label=""
                    v-model="newDoor.door_width"
                    outlined
                    dense
                    height="47"
                    hide-details
                    required
                  ></v-text-field>
                  <span class="cross">X</span>
                  <span class="unit">高</span>
                  <v-text-field
                    class="item_content rounded-0 size"
                    label=""
                    v-model="newDoor.door_height"
                    outlined
                    dense
                    height="47"
                    hide-details
                    required
                  ></v-text-field>
                  <span class="unit">cm</span>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="12">
                  <span class="item_title rounded-0 status">備註</span>
                  <v-textarea
                    class="item_words status"
                    name="input-7-1"
                    label=""
                    v-model="newDoor.remarks"
                    outlined
                    dense
                    height="160"
                    hide-details
                    required
                  ></v-textarea>
                </v-col>
              </v-row>
            </v-card-text>
            <v-card-actions align-center>
              <v-btn @click="confirm" color="success">
                確認
              </v-btn>
            </v-card-actions>
            <!-- <v-snackbar value="false" :timeout="2000" absolute bottom left
            >Your profile has been updated</v-snackbar -->
            >
          </v-card>
          <v-card-actions class="btn_close">
            <v-icon color="black" text @click="closeDailog"
              >mdi-close-circle</v-icon
            >
          </v-card-actions>
        </v-card>
      </v-form>
    </v-dialog>
  </v-row>
</template>
<script>
import { mapActions, mapState } from "vuex";
import { selectsItems } from "../plugins/SelectsItems";
import { formRules } from "../plugins/formRules";

export default {
  status: "DoorCreate",
  created() {
  },
  data() {
    return {
      door_contract: selectsItems.contractSelectsItems,
      door_material: selectsItems.materialSelectsItems,
      door_style: selectsItems.typeSelectsItems,
      formRules: formRules,
      valid: false,
      newDoor: {
        sn: "",
        name: "測試上傳",
        client_name: "Bkeen",
        contact_person: "Dean",
        tax: "034288777",
        fax: "034288776",
        phone: "0955259247",
        city_id: "",
        citystate_id: "",
        address: "測試",
        status: 1,
        effective_date: "2021-05-01",
        door_width: "499",
        door_height: "499",
        door_type: "",
        door_material: "",
        remarks: "",
      },
    };
  },
  methods: {
    ...mapActions("doorsModule", ["showCreateDoorDailog","createNewDoor"]),
    ...mapActions("addressModule", [
      "getCityList",
      "getCityStatesList",
      "getCityName",
    ]),
    confirm() {
      let validate = this.$refs.form.validate();
      if (validate) {
        this.createNewDoor(this.newDoor)
      }
    },
    selectCity(city_id) {
      this.newDoor.citystate_id = null;
      this.getCityStatesList(city_id);
    },
    closeDailog() {
      this.showCreateDoorDailog(false);
    },
  },
  computed: {
    ...mapState("doorsModule", ["isShowCreateDoor"]),
    ...mapState("addressModule", ["citys", "cityStates"]),
  },
  watch: {
    isShowCreateDoor(isShow) {
      if (isShow) {
        this.$refs.form.reset()
        this.getCityList();
      }
    },
  },
};
</script>
