<style scoped>
.v-application--wrap {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
header {
  height: 80px !important;
}
.v-toolbar__content {
  height: auto !important;
}
.v-toolbar__content,
.v-toolbar__extension {
  padding: 10px 16px !important;
}
.v-navigation-drawer {
  position: fixed;
  height: 100vh;
  top: 80px !important;
  width: 260px !important;
}
.v-main {
  margin-left: 260px;
}
.v-sheet.v-app-bar.v-toolbar:not(.v-sheet--outlined) {
  box-shadow: none !important;
}
.v-list-group__header {
  transition: all 0.3s;
}
.active--text {
  background-color: #b4b1a1;
  color: #fff !important;
  transition: all 0.3s;
}
.v-list-item__icon:nth-child(3) {
  display: none !important;
}
.login {
  width: 85%;
  margin: 0 auto;
}
.login_btn {
  width: 100%;
  background-color: #b4b1a1 !important;
  border-color: #b4b1a1 !important;
  box-shadow: none !important;
  /* margin-top: 20px; */
}
.logout_btn {
  background-color: #b4b1a1 !important;
  border-color: #b4b1a1 !important;
}
.v-input__slot {
  margin-bottom: 0;
}
</style>
<template>
  <v-app>
    <v-app-bar app color="brown lighten-5" dark>
      <div class="d-flex align-center">
        <v-img
          alt="Vuetify Logo"
          class="shrink mr-2"
          contain
          src="./assets/logo.svg"
          transition="scale-transition"
          width="130"
        />
      </div>

      <v-spacer></v-spacer>

      <!-- <v-btn
        href="https://github.com/vuetifyjs/vuetify/releases/latest"
        target="_blank"
        text
      >
        <span class="mr-2">Latest Release</span>
        <v-icon>mdi-open-in-new</v-icon>
      </v-btn>-->
    </v-app-bar>

    <v-navigation-drawer color="brown lighten-5" permanent left>
      <v-form v-if="!user" ref="form" lazy-validation class="login">
        <v-text-field
          prepend-inner-icon="mdi-account"
          placeholder="帳號"
          v-model="account"
          :rules="formRules.AccountRules"
          filled
          rounded
          required
        ></v-text-field>
        <!-- v-model="account" -->

        <v-text-field
          prepend-inner-icon="mdi-lock"
          placeholder="密碼"
          :rules="formRules.PasswordRules"
          filled
          rounded
          required
          v-model="password"
        ></v-text-field>
        <!-- v-model="password" -->

        <v-btn
          x-large
          filled
          rounded
          dark
          class="login_btn text-center"
          @click="submit"
          >登入</v-btn
        >
      </v-form>

      <v-list v-else>
        <v-list-item two-line>
          <v-list-item-avatar>
            <v-icon large>mdi-account-circle</v-icon>
          </v-list-item-avatar>

          <v-list-item-content>
            <v-list-item-title>Jane Smith</v-list-item-title>
            <v-list-item-subtitle>
              <v-btn small dark class="logout_btn" @click="logout">
                登出
              </v-btn>
            </v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>

        <v-divider></v-divider>

        <v-list>
          <v-list-group
            v-for="item in menu"
            :key="item.title"
            :prepend-icon="item.icon"
            color="black"
            active-class="active--text"
            @click="goToPath(item.to)"
            no-action
            v-model="item.active"
          >
            <template v-slot:activator>
              <v-list-item-content>
                <v-list-item-title v-text="item.title"></v-list-item-title>
              </v-list-item-content>
            </template>

            <v-list-item
              v-for="child in item.subitems"
              :key="child.title"
              link
              active-class="highlighted"
              :to="child.to"
            >
              <v-list-item-content>
                <v-list-item-title v-text="child.title"></v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list-group>
        </v-list>
      </v-list>
    </v-navigation-drawer>

    <v-main>
      <router-view />
    </v-main>
  </v-app>
</template>

<script>
// import HelloWorld from "./components/HelloWorld";
import mapUrgent from "./components/mapUrgent";
import mapArea from "./components/mapArea";
import DoorList from "./components/DoorList";
import CarList from "./components/CarList";
import AssignList from "./components/AssignList";
import AssignRecord from "./components/AssignRecord";
import LogList from "./components/LogList";
import LogDoorList from "./components/LogDoorList";
import { mapActions, mapState } from "vuex";
import { formRules } from "./plugins/formRules";

export default {
  name: "App",
  components: {
    mapUrgent,
    mapArea,
    DoorList,
    CarList,
    AssignList,
    AssignRecord,
    LogList,
    LogDoorList,
  },
  data: () => ({
    formRules: formRules,
    menu: [
      {
        icon: "mdi-map-outline",
        subitems: [
          { title: "緊急", to: "/mapUrgent" },
          { title: "區域", to: "/mapArea" },
        ],
        title: "即時地圖",
        active: false,
      },
      {
        icon: "mdi-view-list-outline",
        title: "捲門列表",
        subitems: [],
        to: "/doorList",
        active: false,
      },
      {
        icon: "mdi-car",
        title: "車輛列表",
        subitems: [],
        to: "/carList",
        active: false,
      },
      {
        icon: "mdi-post-outline",
        subitems: [
          { title: "派單中", to: "/assignList" },
          { title: "派單紀錄", to: "/assignRecord" },
        ],
        title: "派單",
        active: false,
      },
      {
        icon: "mdi-folder-text",
        subitems: [
          { title: "操作紀錄", to: "/logList" },
          { title: "捲門紀錄", to: "/logDoorList" },
        ],
        title: "紀錄",
        active: false,
      },
    ],
    account: "",
    password: "",
  }),
  computed: {
    ...mapState("userModule", ["status", "user"]),
  },
  methods: {
    ...mapActions("userModule", ["login", "logout"]),
    goToPath: function(path) {
      if (!(path === undefined)) {
        this.$router.push(path).catch((err) => {
          err;
        });
        //console.log("go to:", path);
      }
    },
    submit() {
      let validate = this.$refs.form.validate();
      console.log("submit:", validate);
      if (validate) {
        let { account, password } = this;
        this.login({ account, password });
      }
    },
  },
  watch: {
    $route(to) {
      if (to.matched.length >= 1) {
        const path = to.matched.filter((m) => !m.parent)[0].path;
        const item = this.menu.find((item) => {
          if (item.to) {
            return item.to === path;
          }
          return item.subitems.find((item) => item.to === path) ? true : false;
        });
        if (item) item.active = true;
      }
    },
  },
};
</script>
