<style scoped>
.map,
iframe {
  width: 100%;
  height: 500px;
}
.text-green {
  color: #43a047;
  position: relative;
}
.text-green:before {
  content: "";
  width: 12px;
  height: 12px;
  background-color: #43a047;
  display: block;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 18px;
  margin-left: -30px;
}
.text-red {
  position: relative;
}
.text-red:before {
  content: "";
  width: 12px;
  height: 12px;
  background-color: red;
  display: block;
  border-radius: 10px;
  position: absolute;
  left: 50px;
  top: 18px;
}
.btn_close {
  position: absolute;
  right: 0;
  top: 0;
}
.item_title {
  min-width: 100px;
  display: inline-block;
  padding: 12px 16px;
  background-color: #f5f5f5;
  height: 48px;
  line-height: 24px;
  vertical-align: middle;
}
.item_title.date {
  width: 160px;
}
.item_title.door_status {
  position: relative;
}
.cross {
  width: 25px;
  display: inline-block;
  padding: 12px 0 12px 16px;
}
.unit {
  width: 44px;
  display: inline-block;
  padding: 12px 0 12px 16px;
}
.item_content {
  width: calc(100% - 100px);
  display: inline-block;
  /* outline: 1px solid #f5f5f5; */
  height: 46px;
  line-height: 46px;
  vertical-align: middle;
  margin-top: 0;
}
.item_words {
  width: calc(100% - 100px);
  display: inline-block;
  outline: 1px solid #f5f5f5;
  /* padding: 8px !important; */
  vertical-align: middle;
  margin-top: 0;
}
.item_content.city,
.item_content.village {
  width: calc((100% - 125px) / 2);
}
.item_content.city {
  margin-right: 25px;
}
.item_content.road {
  width: 100%;
}
.item_content.size {
  width: calc((100% - 257px) / 2);
}
.item_content.date {
  width: calc(100% - 160px);
}
.theme--light.v-data-table
  > .v-data-table__wrapper
  > table
  > tbody
  > tr:hover:not(.v-data-table__expanded__content):not(.v-data-table__empty-wrapper) {
  background-color: #fff;
}
.status {
  height: 158px;
}
.v-card__actions > .v-btn.v-btn {
  margin: 0 auto;
}
.v-text-field--outlined fieldset {
  border-color: rgba(0 0 0 0.1) !important;
}
</style>

<template>
  <v-row justify="center">
    <v-dialog v-model="isShowCreateAssign" persistent max-width="1000px">
      <!-- value=true -->
      <v-form ref="form" v-model="valid">
        <v-card>
          <v-card class="overflow-hidden" color flat>
            <v-card-title>
              <h3 class="text-center pa-6 mx-auto">新增派單</h3>
            </v-card-title>
            <v-card-text>
              <v-row>
                <v-col cols="12" class="item">
                  <span class="item_title">捲門序號</span>

                  <v-select
                    v-if="initialSelected == null"
                    class="item_content rounded-0"
                    v-model="selected_door"
                    :items="needAssignDoorList"
                    outlined
                    dense
                    height="47"
                    @change="selectNeedAssignSN"
                  >
                    <template v-slot:selection="{ item }">
                      <Doorstatus :doorStatus="item.status" />
                      <span class="pl-4">{{ item.door_sn }}</span>
                    </template>
                    <template v-slot:item="{ item, attrs, on }">
                      <v-list-item v-on="on" v-bind="attrs">
                        <v-list-item-content>
                          <v-list-item-title>
                            <v-row no-gutters align="center">
                              <Doorstatus :doorStatus="item.status" />
                              <v-spacer></v-spacer>
                              <span>{{ item.door_sn }}</span>
                            </v-row>
                          </v-list-item-title>
                        </v-list-item-content>
                      </v-list-item>
                    </template>
                  </v-select>
                  <Doorstatus v-else :doorStatus="selected_door.status" />
                </v-col>
                <v-col cols="4" class="item">
                  <span class="item_title door_status">狀態</span>
                  <div class="item_content rounded-0 text_status pl-10">
                    <Doorstatus :doorStatus="door.status" />
                  </div>
                </v-col>
                <v-col cols="4" class="item">
                  <span class="item_title">客戶名稱</span>
                  <div class="item_content rounded-0 pl-4">
                    {{ door.client_name }}
                  </div>
                </v-col>
                <v-col cols="4" class="item">
                  <span class="item_title">捲門名稱</span>
                  <div class="item_content rounded-0 pl-4">
                    {{ door.name }}
                  </div>
                </v-col>
                <v-col cols="6" class="item">
                  <span class="item_title">聯絡人</span>
                  <div class="item_content rounded-0 pl-4">
                    {{ door.contact_person }}
                  </div>
                </v-col>
                <v-col cols="6">
                  <span class="item_title">手機</span>
                  <div class="item_content rounded-0 pl-4">
                    {{ door.phone }}
                  </div>
                </v-col>
                <v-col cols="12" class="d-flex flex-row">
                  <span class="item_title">地址</span>
                  <v-dialog v-model="dialog" width="800">
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        plain
                        small
                        v-bind="attrs"
                        v-on="on"
                        class="map_action mt-2 ml-4"
                        color="#877C45"
                      >
                        <v-icon dark class="mr-1">
                          mdi-google-maps
                        </v-icon>
                        地圖
                      </v-btn>
                    </template>

                    <v-card>
                      <v-card-text class="pa-4">
                        <span class="map">
                          <iframe
                            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d14462.28307777603!2d121.19664106977541!3d25.014697099999992!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x346821677632f263%3A0xf5660d8237d5a781!2z6Iqx6Iie6LGs77yI6Z-T5byP54Ok6IKJ5ZCD5Yiw6aO977yJ!5e0!3m2!1szh-TW!2stw!4v1618325625599!5m2!1szh-TW!2stw"
                            style="border: 0"
                            allowfullscreen
                            loading="lazy"
                          ></iframe>
                        </span>
                      </v-card-text>

                      <v-divider></v-divider>
                    </v-card>
                  </v-dialog>
                  <div class="item_content pl-4">
                    {{ door.city_id }}{{ door.citystate_id }}{{ door.address }}
                  </div>
                </v-col>
                <v-col cols="6" class="d-flex flex-row">
                  <span class="item_title">指派車輛</span>
                  <v-dialog v-model="dialog" width="800">
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        plain
                        small
                        v-bind="attrs"
                        v-on="on"
                        class="map_action mt-2"
                        color="#877C45"
                      >
                        <v-icon dark class="mr-1">
                          mdi-map
                        </v-icon>
                        地圖選擇
                      </v-btn>
                    </template>

                    <v-card>
                      <v-card-text class="pa-4">
                        <span class="map">
                          <iframe
                            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d14462.28307777603!2d121.19664106977541!3d25.014697099999992!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x346821677632f263%3A0xf5660d8237d5a781!2z6Iqx6Iie6LGs77yI6Z-T5byP54Ok6IKJ5ZCD5Yiw6aO977yJ!5e0!3m2!1szh-TW!2stw!4v1618325625599!5m2!1szh-TW!2stw"
                            style="border: 0"
                            allowfullscreen
                            loading="lazy"
                          ></iframe>
                        </span>
                      </v-card-text>

                      <v-divider></v-divider>
                    </v-card>
                  </v-dialog>
                  <v-select
                    class="item_content rounded-0"
                    item-text="name"
                    item-value="id"
                    color="#eee"
                    label=""
                    value="AA"
                    outlined
                    dense
                    height="47"
                    hide-details
                  ></v-select>
                </v-col>
                <v-col cols="6">
                  <span class="item_title date">指派日期</span>
                  <v-menu
                    ref="menu"
                    v-model="menu"
                    :close-on-content-click="false"
                    :return-value.sync="newAssign.assigned_date"
                    transition="scale-transition"
                    offset-y
                    min-width="auto"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        v-model="newAssign.assigned_date"
                        label=""
                        prepend-inner-icon="mdi-calendar"
                        readonly
                        v-bind="attrs"
                        v-on="on"
                        class="item_content date rounded-0"
                        outlined
                        dense
                        height="47"
                        hide-details
                      ></v-text-field>
                    </template>
                    <v-date-picker
                      v-model="newAssign.assigned_date"
                      no-title
                      scrollable
                    >
                      <v-spacer></v-spacer>
                      <v-btn text color="primary" @click="menu = false">
                        Cancel
                      </v-btn>
                      <v-btn
                        text
                        color="primary"
                        @click="$refs.menu.save(newAssign.assigned_date)"
                      >
                        OK
                      </v-btn>
                    </v-date-picker>
                  </v-menu>
                </v-col>
                <v-col cols="12">
                  <span class="item_title status">備註</span>
                  <v-textarea
                    class="item_words status rounded-0"
                    name="input-7-1"
                    v-model="newAssign.remark"
                    label=""
                    outlined
                    dense
                    height="158"
                    hide-details
                    no-resize
                    prop
                  ></v-textarea>
                </v-col>
              </v-row>
            </v-card-text>
            <v-card-actions align-center>
              <!-- <v-btn @click="isEditing = !isEditing" color="success">
              刪除
            </v-btn> -->
              <v-btn
                :disabled="dialog"
                :loading="dialog"
                class="white--text mb-4"
                color="#AAA481"
                @click="confirm"
              >
                新增
              </v-btn>
              <v-dialog v-model="dialog" persistent width="300">
                <v-card color="#AAA481" dark>
                  <v-card-text>
                    資料新增中
                    <v-progress-linear
                      indeterminate
                      color="white"
                      class="mb-0"
                    ></v-progress-linear>
                  </v-card-text>
                </v-card>
              </v-dialog>
            </v-card-actions>
          </v-card>
          <v-card-actions class="btn_close">
            <v-icon color="black" text @click="closeDailog"
              >mdi-close-circle</v-icon
            >
          </v-card-actions>
        </v-card>
      </v-form>
    </v-dialog>
  </v-row>
</template>
<script>
import { mapActions, mapState } from "vuex";
import moment from "moment";
import Doorstatus from "./plugins/DoorStatus";

export default {
  props: ["initialSelected"],
  status: "AssignCreate",
  components: {
    Doorstatus,
  },
  created() {
    if (this.initialSelected != null) {
      this.selected_door = this.initialSelected;
      this.selectNeedAssignSN();
    } else this.getNeedAssignDoors();
  },
  data() {
    return {
      date: "",
      valid: false,
      menu: false,
      dialog: false,
      selected_door: {},
      newAssign: {
        door_id: "",
        doorStatus_id: 0,
        car_plate: "",
        remark: "",
        assigned_date: moment().format("yyyy-MM-DD"),
      },
    };
  },
  methods: {
    ...mapActions("doorsModule", ["getDoorInfo"]),
    ...mapActions("assignModule", [
      "getNeedAssignDoors",
      "showCreateAssignDailog",
      "createNewAssign",
    ]),
    closeDailog() {
      this.showCreateAssignDailog({ isShow: false, view: "" });
      this.selected_door = {};
    },
    selectNeedAssignSN() {
      console.log("get:", this.selected_door);
      this.newAssign.door_id = this.selected_door.door_id;
      this.newAssign.doorStatus_id = this.selected_door.status_id;
      this.getDoorInfo(this.newAssign.door_id);
    },
    confirm() {
      let validate = this.$refs.form.validate();
      if (validate) {
        this.newAssign.car_plate = "AAA-5555";
        this.createNewAssign(this.newAssign);
      }
    },
  },
  computed: {
    ...mapState("doorsModule", ["door"]),
    ...mapState("assignModule", ["isShowCreateAssign", "needAssignDoorList"]),
  },
  watch: {
    dialog(val) {
      if (!val) return;
      setTimeout(() => (this.dialog = false), 4000);
    },
  },
};
</script>
