<template>

    <v-sheet
      height="100%"
      class="overflow-hidden"
    >
      <v-navigation-drawer
        v-model="showUrgentList"
        fixed
        top
        right
        temporary
        width="50%"
      >
        
        <div class="py-3">
            <v-btn
                class="mt-6"
                text
                @click="selectUrgentCity(undefined)"
            >
                <v-icon>mdi-close-circle</v-icon>
            </v-btn>

            <v-col class="text-center">
                <h3 class="mb-6">緊急清單</h3>
                <v-simple-table>
                    <template v-slot:default>
                        <thead>
                            <tr>
                            <th class="text-center" width="100">狀態</th>
                            <th class="text-center">捲門序號</th>
                            <th class="text-center">名稱</th>
                            <th class="text-center">客戶</th>
                            <th class="text-center">Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="item in doors" :key="item.id">
                            <td>
                                <v-chip class="ma-2" :class="getStatusColor(item.status)" :text-color="getStatusTextColor(item.status)">
                                {{ getStatusTitle(item.status) }}
                                </v-chip>
                            </td>
                            <td>{{item.sn}}</td>
                            <td>{{item.name}}</td>
                            <td>{{ item.client_name}}</td>
                            <td>
                                <v-btn text @click="openCreateAssign(item)">派單</v-btn>
                            </td>
                            </tr>
                        </tbody>
                    </template>
                </v-simple-table>
            </v-col>
            <div class="pageinfo">
                <v-container class="max-width">
                <v-pagination
                    v-model="pagination.currentPage"
                    class="my-4"
                    :length="pagination.totalPage"
                    @input="handlePageChange"
                ></v-pagination>
                </v-container>
            </div>

            <component :is="showView" :initialSelected="selectedDoor"></component>
    <!-- <AssignCreate /> -->
            <!-- <v-dialog
                v-model="dialog"
                width="100%"
                height="100%"
                fixed
                overlay-opacity=".8"
            >
                <v-card>
                    <v-card-title class="headline grey lighten-2">
                        Privacy Policy
                    </v-card-title>
        
                </v-card>
            </v-dialog> -->
        </div>
      </v-navigation-drawer>
    </v-sheet>
</template>

<script>
import { mapActions, mapState } from "vuex";
import { statusConvert } from "../plugins/statusConvert";
import AssignCreate from "./AssignCreate";

export default {
  status: "DoorUrgentList.",
  created() {
      console.log("Create DoorUrgent")
      this.selectUrgentCity(undefined)
  },
  components: {
    AssignCreate,
  },
  data() {
    return {
      drawer: null,
      selectedDoor:null,
      };
  },
  methods: {
    ...mapActions("doorsModule", [
      "getDoorsByCityAndStatus",
    ]),
    ...mapActions("doorsStatusModule", ["selectUrgentCity"]),
    ...mapActions("assignModule", [
      "showCreateAssignDailog",
    ]),
    getStatusColor: statusConvert.getStatusColor,
    getStatusTitle: statusConvert.getStatusTitle,
    getStatusTextColor: statusConvert.getStatusTextColor,
    goToInfo(door) {
      this.chooseDoor(door);
      this.showInfoDailog(true);
    },
    handlePageChange(currentPage) {
        let city_id = this.selectedUrgentCity.city_id
        let status = 0
        this.getDoorsByCityAndStatus({status, city_id, currentPage})
    },
    openCreateAssign(door){
      console.log(door)
      door.door_sn = door.sn
      door.door_id = door.id
      this.selectedDoor = door



      this.showCreateAssignDailog({ isShow:true, view: AssignCreate })
    },
  },
  computed: {
    ...mapState("doorsStatusModule", ["selectedUrgentCity","showUrgentList"]),
    ...mapState("doorsModule", ["doors", "pagination"]),
    ...mapState("assignModule", ["showView"]),
    dailogComponent() {
      return this.dialogTag;
    }
  },
  watch:{
    showUrgentList(show){
      if(show){
        console.log("selectUrgentCity:",this.selectedUrgentCity)
        let city_id = this.selectedUrgentCity.city_id
        let status = 0
        let currentPage = this.pagination.currentPage
         this.getDoorsByCityAndStatus({status, city_id,currentPage})
      }
    }
  }
};
</script>
