import axios from "axios";



const URL = "address/";

function GetCityList(token) {
    return axios
        .get(process.env.VUE_APP_API_URL + URL + "city", {
            headers: { 'authorization': token }
        })
        .catch(function(error) {
            return error;
        })
        .then(function(response) {
            if (response == null) return null;
            return response.data;
        })
}

function GetCityStatesListByCity(token, city_id) {
    console.log("Get GetCityStatesListByCity:", city_id)
    return axios
        .get(process.env.VUE_APP_API_URL + URL + "citystate", {
            headers: { 'authorization': token },
            params: {
                cityId: city_id
            },
        })
        .catch(function(error) {
            return error;
        })
        .then(function(response) {
            if (response == null) return null;
            return response.data;
        })
}

function GetCityAndStatesName(token, city_id, cityStates_id) {
    console.log("Get GetCityAndStatesName:", city_id, cityStates_id)
    return axios
        .get(process.env.VUE_APP_API_URL + URL + "citystatename", {
            headers: { 'authorization': token },
            params: {
                cityId: city_id,
                cityStateId: cityStates_id,
            },
        })
        .catch(function(error) {
            return error;
        })
        .then(function(response) {
            if (response == null) return null;
            return response.data;
        })
}

export const addressService = {
    GetCityList,
    GetCityStatesListByCity,
    GetCityAndStatesName
};