import { assignService } from '../service/assign.service';
import { doorService } from '../service/door.service.js';
import config from '../config'

const TAG = "AssignModule"
const state = {
    isShowCreateAssign: false,
    isShowAssignInfo: false,
    needAssignDoorList: [],
    assignList: [],
    pagination: {
        currentPage: 1,
        totalItem: 0,
        totalPage: 0,
    },
    selectedAssign: {},
    showView: ""
}

const actions = {
    createNewAssign({ commit, rootState, state, dispatch }, newAssign) {
        let token = rootState.userModule.user.token
        console.log(TAG, "createNewDoor:", newAssign)
        assignService.create(token, newAssign)
            .then(
                data => {
                    console.log(TAG, "Create Assign:", data)
                    commit("showCreateAssign", false)
                    dispatch("getAllAssignings", state.pagination.currentPage)
                },
                error => {
                    console.log(TAG, "Create door:", error)
                }
            )
    },
    getAllAssignings({ commit, rootState }, currentPage) {
        // dispatch("doorsModule/getAllDoorByNeedAssign", [0, 1, 2, 4, 5], { root: true })
        let token = rootState.userModule.user.token
        assignService.getAllAssigning(token, currentPage)
            .then(
                data => {
                    if (data.code == config.RES_CODE.SUCCESS) {
                        let payload = data.data
                        commit("setAssignList", payload.data)
                        commit("setPagination", {
                            currentPage: currentPage,
                            totalItem: payload.totalCount,
                            totalPage: Math.ceil(payload.totalCount / 10)
                        })
                    }
                },
                error => {
                    console.log(TAG, "vuex get all door:", error)
                }
            )
    },
    getNeedAssignDoors({ commit, rootState }) {
        // dispatch("doorsModule/getAllDoorByNeedAssign", [0, 1, 2, 4, 5], { root: true })
        let token = rootState.userModule.user.token
        doorService.getAllByNeedAssign(token, [0, 1, 2, 4, 5])
            .then(
                data => {
                    console.log(TAG, "Get Door By Status:", data)
                    if (data.code == config.RES_CODE.SUCCESS) {
                        let payload = data.data
                        commit("setNeedAssignList", payload.data)
                    }
                },
                error => {
                    console.log(TAG, "vuex get all door:", error)
                }
            )
    },
    closeAssign({ commit, rootState, state, dispatch }, assign) {
        let token = rootState.userModule.user.token
            // console.log(TAG, "closeAssign:", assign)
        assignService.closeAssign(token, assign)
            .then(
                data => {
                    console.log(TAG, "Close Assign:", data)
                    if (data.code == config.RES_CODE.SUCCESS) {
                        commit("showAssignInfo", false)
                        dispatch("getAllAssignings", state.pagination.currentPage)
                    }
                },
                error => {
                    console.log(TAG, "Create door:", error)
                }
            )
    },
    showCreateAssignDailog({ commit, dispatch }, { isShow, view }) {
        console.log(TAG, "Create isShow:", view)
        commit("setView", view)
        commit("showCreateAssign", isShow)
        dispatch("doorsModule/closeDoorInfo", null, { root: true })
    },
    showAssignInfoDailog({ commit }, isShow) {
        commit("showAssignInfo", isShow)
    },
    setAssignInfoPage({ commit }, assign) {
        commit("setSelectedAssignInfo", assign)
    },
    showView({ commit }, view) {
        commit("setView", view)
    },
    closeAllView({ commit }) {
        commit("setView", "")
    }
};

const mutations = {
    showCreateAssign(state, show) {
        state.isShowCreateAssign = show;
    },
    setNeedAssignList(state, list) {
        state.needAssignDoorList = list
    },
    setAssignList(state, list) {
        state.assignList = list
    },
    showAssignInfo(state, show) {
        state.isShowAssignInfo = show;
    },
    setSelectedAssignInfo(state, assign) {
        state.selectedAssign = assign;
    },
    setPagination(state, payload) {
        state.pagination = payload;
    },
    setView(state, view) {
        state.showView = view;
    },
};

export const assignModule = {
    namespaced: true,
    state,
    actions,
    mutations
};