<style scope>
.v-main {
  padding: 80px 0px 0px !important;
}
.v-main__wrap {
  position: absolute;
  right: 0;
  top: 0;
  /* padding-top: 80px; */
}
.v-application--wrap {
  min-height: auto !important;
}
tr .text-green {
  color: #43a047;
  position: relative;
}
.text-green:before {
  content: "";
  width: 12px;
  height: 12px;
  background-color: #43a047;
  display: block;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 18px;
  margin-left: -30px;
}
.text-red {
  color: red;
  position: relative;
}
.text-red:before {
  content: "";
  width: 12px;
  height: 12px;
  background-color: red;
  display: block;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 18px;
  margin-left: -30px;
}
.pageinfo {
  width: 80%;
  max-width: 900px;
  position: fixed;
  bottom: 0;
  left: 50%;
  margin-left: -320px;
}
.theme--light.v-btn.btn_add {
  color: #fff;
}
</style>
<template>
  <v-container>
    <v-row>
      <v-col cols="4">
        <v-text-field
          solo
          rounded
          label="search"
          prepend-inner-icon="mdi-magnify"
          small
        ></v-text-field>
      </v-col>
      <v-col cols="6"></v-col>
      <v-col cols="2">
        <div class="text-center">
          <v-btn rounded color="brown lighten-1" class="btn_add" @click="openCreateDailog">
            <v-icon left>
              mdi-plus-circle
            </v-icon>
            新增車輛
          </v-btn>
        </div>
      </v-col>
      <v-col cols="12" class="text-center">
        <v-app id="inspire">
          <v-simple-table>
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-center" width="100">SIM卡序號</th>
                  <th class="text-center">車牌號碼</th>
                  <th class="text-center">備註</th>
                  <th class="text-center">Actions</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="item in carlist" :key="item.sn">
                  <td>{{ item.sn }}</td>
                  <td>{{ item.car }}</td>
                  <td>{{ item.status }}</td>
                  <td>
                    <v-btn text @click="goToInfo(item)">詳細內容</v-btn>
                  </td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </v-app>
      </v-col>
      <div class="pageinfo">
        <v-container class="max-width">
          <v-pagination
            v-model="currentPage"
            class="my-4"
            :length="pagination.totalPage"
            @input="handlePageChange"
          ></v-pagination>
        </v-container>
      </div>

    </v-row>
    <DoorInfo/>
    <DoorCreate />
    <!-- <DoorInfo :selected-door="selectedDoor" /> -->
    <!-- <component :is="dailogComponent" ></component> -->
  </v-container>
</template>

<script>
import { mapActions, mapState } from "vuex";
import DoorInfo from "./DoorInfo";
import DoorCreate from "./DoorCreate";

export default {
  status: "DoorList",
  created() {
    const { currentPage } = this;
    this.getAll({ currentPage });
  },
  components: {
    DoorInfo,
    DoorCreate,
  },
  data() {
    return {
      carlist: [
        {
          sn: "1121321321231231",
          car: "ABC-1234",
          status: "1",
        },
        {
          sn: "1121321321231231",
          car: "ABC-1234",
          status: "1",
        },
        {
          sn: "1121321321231231",
          car: "ABC-1234",
          status: "1",
        },
        {
          sn: "1121321321231231",
          car: "ABC-1234",
          status: "1",
        },
        {
          sn: "1121321321231231",
          car: "ABC-1234",
          status: "1",
        }
      ],
      currentPage: 1,
      dialogTag: "",
      // selectedDoor: {},
    };
  },
  methods: {
    ...mapActions("doorsModule", ["getAll", "chooseDoor", "showInfoDailog","showCreateDoorDailog"]),
    getStatusColor: function(status) {
      switch (status) {
        case 0:
          return "text-green";
        case 1:
          return "text-red";
        case 2:
          return "text-green";
        case 3:
          return "text-green";
        case 4:
          return "text-green";
        case 5:
          return "text-green";
        case 6:
          return "text-green";
        case 7:
          return "text-green";
        default:
          return "text-green";
      }
    },
    getStatusTitle: function(status) {
      switch (status) {
        case 0:
          return "正常";
        case 1:
          return "故障";
        case 2:
          return "";
        case 3:
          return "orange";
        case 4:
          return "black";
        case 5:
          return "yellow";
        case 6:
          return "gpstimeerror";
        case 7:
          return "red";
        default:
          return "";
      }
    },
    goToInfo(door) {
      this.chooseDoor(door);
      this.showInfoDailog(true);
    },
    handlePageChange(currentPage) {
      this.getAll({ currentPage });
    },
    openCreateDailog(){
      this.showCreateDoorDailog(true)
    }
  },
  computed: {
    ...mapState("doorsModule", ["doors", "pagination"]),
    dailogComponent() {
      return this.dialogTag;
    },
  },
};
</script>
