import { addressService } from '../service/address.service';
import config from '../config'

const TAG = "AddressModule"
const state = {
    citys: [],
    cityStates: [],
    cityStateName: {}
}

const actions = {
    getCityList({ commit, rootState }) {
        let token = rootState.userModule.user.token
        addressService.GetCityList(token)
            .then(
                data => {
                    if (data.code == config.RES_CODE.SUCCESS) {
                        let payload = data.data
                        console.log(TAG, "Get Citys:", payload)
                        commit("setCitys", payload)
                    }
                },
                error => {
                    console.log(TAG, "vuex error:", error)
                }
            )
    },
    getCityStatesList({ commit, rootState }, city_id) {
        let token = rootState.userModule.user.token
        addressService.GetCityStatesListByCity(token, city_id)
            .then(
                data => {
                    if (data.code == config.RES_CODE.SUCCESS) {
                        let payload = data.data
                        console.log(TAG, "Get CityStates:", payload)
                        commit("setCityStates", payload)
                    }
                },
                error => {
                    console.log(TAG, "vuex error:", error)
                }
            )
    },
    getCityAndStateName({ commit, rootState }, { city_id, citystate_id }) {
        let token = rootState.userModule.user.token
        addressService.GetCityAndStatesName(token, city_id, citystate_id)
            .then(
                data => {
                    if (data.code == config.RES_CODE.SUCCESS) {
                        let payload = data.data
                        console.log(TAG, "Get getCityAndStateName:", payload)
                        commit("setCityStateName", payload)
                    }
                },
                error => {
                    console.log(TAG, "vuex error:", error)
                }
            )
    }
};

const mutations = {
    setCitys(state, citys) {
        state.citys = citys;
    },
    setCityStates(state, cityStates) {
        state.cityStates = cityStates;
    },
    setCityStateName(state, cityState) {
        state.cityStateName = cityState
    }
};

export const addressModule = {
    namespaced: true,
    state,
    actions,
    mutations
};