import axios from 'axios'
const TAG = "DoorsService"
const URL = "door/"

function createNewDoor(token, door) {
    console.log(TAG, "Call createNewDoorApi")
    return axios.post(process.env.VUE_APP_API_URL + URL + 'add', door, {
            headers: { 'authorization': token },
        })
        .then(response => {
            return response.data
        })
        .catch(function(error) {
            console.log(error);
            return null;
        })
}


function getAll(token, currentPage) {
    console.log(TAG, "Call GetalldoorApi")
    return axios.get(process.env.VUE_APP_API_URL + URL + 'list', {
            headers: { 'authorization': token },
            params: {
                currentPage: currentPage
            },
        })
        .then(response => {
            return response.data
        })
        .catch(function(error) {
            console.log(error);
            return null;
        })
}


