import { historyService } from '../service/history.service.js';
import config from '../config'

const TAG = "HistoryModule"
const state = {
    doorhistoryList: [],
    pagination: {
        currentPage: 1,
        totalItem: 0,
        totalPage: 0,
    }
}

const actions = {
    getDoorHistoryList({ commit, rootState }, currentPage) {
        // dispatch("doorsModule/getAllDoorByNeedAssign", [0, 1, 2, 4, 5], { root: true })
        let token = rootState.userModule.user.token
        historyService.getDoorHistoryList(token, currentPage)
            .then(
                data => {
                    if (data.code == config.RES_CODE.SUCCESS) {
                        let payload = data.data
                            // console.log("getDoorHistoryList:", payload.data)
                        commit("setDoorHistoryList", payload.data)
                        commit("setPagination", {
                            currentPage: currentPage,
                            totalItem: payload.totalCount,
                            totalPage: Math.ceil(payload.totalCount / 10)
                        })
                    }
                },
                error => {
                    console.log(TAG, "vuex get all door:", error)
                }
            )
    }
};

const mutations = {
    setDoorHistoryList(state, list) {
        state.doorhistoryList = list
    },
    setPagination(state, payload) {
        state.pagination = payload;
    },
};

export const historyModule = {
    namespaced: true,
    state,
    actions,
    mutations
};