<style scope>
.v-main {
  padding: 80px 0px 0px !important;
}
.v-main__wrap {
  position: absolute;
  right: 0;
  top: 0;
  /* padding-top: 80px; */
}
.v-application--wrap {
  min-height: auto !important;
}
tr .text-green {
  color: #43a047;
  position: relative;
}
.text-green:before {
  content: "";
  width: 12px;
  height: 12px;
  background-color: #43a047;
  display: block;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 18px;
  margin-left: -30px;
}
.text-red {
  color: red;
  position: relative;
}
.text-red:before {
  content: "";
  width: 12px;
  height: 12px;
  background-color: red;
  display: block;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 18px;
  margin-left: -30px;
}
.pageinfo {
  width: 80%;
  max-width: 900px;
  position: fixed;
  bottom: 0;
  left: 50%;
  margin-left: -320px;
}
.theme--light.v-btn.btn_add {
  color: #fff;
}
</style>
<template>
  <v-container>
    <v-row>
      <v-col cols="4">
        <v-text-field
          solo
          rounded
          label="search"
          prepend-inner-icon="mdi-magnify"
          small
        ></v-text-field>
      </v-col>
      <v-col cols="8"></v-col>
      <v-col cols="12" class="text-center">
        <!-- <v-app id="inspire"> -->
        <v-simple-table>
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-center" width="100">狀態</th>
                <th class="text-center">發生時間</th>
                <th class="text-center">捲門序號</th>
                <th class="text-center">捲門</th>
                <th class="text-center">客戶</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="item in doorhistoryList" :key="item.id">
                <td>
                  <Doorstatus :doorStatus="item.doorStatus"/>
                </td>
                <td>{{ formatdDate(item.statusTime)}}</td>
                <td>{{ item.sn }}</td>
                <td>{{ item.name }}</td>
                <td>{{ item.client_name }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
        <!-- </v-app> -->
      </v-col>
      <div class="pageinfo">
        <v-container class="max-width">
          <v-pagination
            v-model="pagination.currentPage"
            class="my-4"
            :length="pagination.totalPage"
            @input="handlePageChange"
          ></v-pagination>
        </v-container>
      </div>
    </v-row>
  </v-container>
</template>

<script>
import { mapActions, mapState } from "vuex";
import moment from "moment";
import Doorstatus from "./plugins/DoorStatus";
export default {
  status: "LogList",
  components:{
    Doorstatus
  },
  data() {
    return {
      logDoorList: [
        {
          number: "RTU1233455",
          status: "故障",
          company: "聯上世界",
          car: "ABC-1234",
          time: "2021-12-12  20:30",
          action: "詳細內容",
        },
        {
          number: "RTU1233455",
          status: "故障",
          company: "聯上世界",
          car: "ABC-1234",
          time: "2021-12-12  20:30",
          action: "詳細內容",
        },
        {
          number: "RTU1233455",
          status: "故障",
          company: "聯上世界",
          car: "ABC-1234",
          time: "2021-12-12  20:30",
          action: "詳細內容",
        },
      ],
      dialog: false,
    };
  },
  created() {
    this.getDoorHistoryList(this.pagination.currentPage);
  },
  methods: {
    ...mapActions("historyModule", ["getDoorHistoryList"]),
    handlePageChange(currentPage) {
      this.getDoorHistoryList(currentPage);
    },
    formatdDate(date) {
      return moment(date).format("YYYY-MM-DD HH:mm:ss");
    },
  },
  computed: {
    ...mapState("historyModule", ["doorhistoryList", "pagination"]),
  },
};
</script>
