import axios from "axios";


const URL = "history/";

export const historyService = {
    getDoorHistoryList,
};

function getDoorHistoryList(token, currentPage) {
    return axios.get(process.env.VUE_APP_API_URL + URL + 'doors', {
            headers: { 'authorization': token },
            params: {
                currentPage: currentPage
            },
        })
        .then(response => {
            return response.data
        })
        .catch(function(error) {
            console.log(error);
            return null;
        })
}