import { userService } from '../service/user.service.js';
import config from '../config'
const TAG = "userModule"
const user = JSON.parse(localStorage.getItem('user'));

const state = user ? {
    status: { loginIn: true },
    user: user,
    isAdmin: (user.account.role == 0)
} : {
    // status: {},
    status: { loginIn: true },
    user: null,
    isAdmin: false
};

const actions = {
    login({ /*dispatch,*/ commit }, { account, password }) {
        // commit('loginRequest', { username });
        userService.login(account, password)
            .then(
                data => {
                    // commit('loginSuccess', user);
                    // router.push('/');

                    console.log(TAG, data);
                    if (data.code == config.RES_CODE.SUCCESS) {
                        let user = data.data;

                        if (user.token) {
                            console.log(TAG, user.token);
                            localStorage.setItem('user', JSON.stringify(user));
                            commit('loginSuccess', user)
                        }


                    } else if (data.code == config.RES_CODE.LOGIN_FAIL) {
                        commit('loginFailure')
                    }
                },
                error => {
                    // commit('loginFailure', error);
                    // dispatch('alert/error', error, { root: true });
                    console.log(TAG, error);
                }
            );
    },
    logout({ commit }) {
        userService.logout();
        commit('logout');
    },
};

const mutations = {
    loginSuccess(state, user) {
        state.status = { loginIn: true };
        state.user = user;
        state.isAdmin = (user.account.role == 0);
    },
    loginFailure(state) {
        state.status = {};
        state.user = null;
        state.isAdmin = false;
    },
    logout(state) {
        state.status = {};
        state.user = null;
        state.isAdmin = false;
    },
};

export const userModule = {
    namespaced: true,
    state,
    actions,
    mutations
};